import axios from "axios"
import {
  REQUEST_SWAP,
  UPDATE_SWAPS,
  OPEN_MODAL,
  ACCEPT_SWAP,
  DELETE_SWAP,
  ADD_MESSAGE,
  SEE_MESSAGE
} from "./types"

export const requestSwap = swapRequest => dispatch => {
  axios
    .post("/swaps", swapRequest)
    .then(res => {
      dispatch({
        type: REQUEST_SWAP,
        payload: res.data
      })
    })
    .catch(err => {
      dispatch({
        type: OPEN_MODAL,
        payload: {
          binary: false,
          input: false,
          type: "error",
          message: "swap failed"
        }
      })
    })
}
export const getSwaps = id => dispatch => {
  console.log("getting swaps")
  axios
    .get(`/swaps/${id}`)
    .then(res => {
      dispatch({
        type: UPDATE_SWAPS,
        payload: res.data.results
      })
    })
    .catch(err => {
      dispatch({
        type: UPDATE_SWAPS,
        payload: ["error 404"]
      })
    })
}
export const acceptSwap = id => dispatch => {
  axios
    .patch(`/swaps/accept/${id}`)
    .then(res => {
      dispatch({
        type: ACCEPT_SWAP,
        payload: id
      })
    })
    .catch(err => {
      dispatch({
        type: ADD_MESSAGE,
        payload: {
          id: id,
          sender: "",
          message: "error"
        }
      })
    })
}
export const deleteSwap = id => dispatch => {
  axios
    .delete(`/swaps/${id}`)
    .then(res => {
      dispatch({
        type: DELETE_SWAP,
        payload: id
      })
    })
    .catch(err => {
      dispatch({
        type: UPDATE_SWAPS,
        payload: ["error 404"]
      })
    })
}
export const addMessage = message => dispatch => {
  let mes = {
    requester: message.requester,
    receiverId: message.receiverId,
    sender: message.sender,
    message: message.message,
    time: message.time
  }
  dispatch({
    type: ADD_MESSAGE,
    payload: message
  })
  console.log("/swaps/message.id sent", mes)
  axios
    .patch(`/swaps/${message.id}`, mes)
    .then(() => console.log("addMessage success"))
    .catch(() => console.log("addMessage error"))
}

export const seeMessage = swap => dispatch => {
  console.log(swap)
  axios.patch(`/swaps/seen/${swap}`).then(res => {
    console.log("swapsies ", swap)
    dispatch({
      type: SEE_MESSAGE,
      payload: swap
    })
  })
}
